<template>
  <div id="home">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" :show-indicators=false>
      <van-swipe-item v-for="(val) in bannerData" :key="val._id">
        <img :src=val.banner alt="">
        <div class="text-box">
          <h2>{{ val.title }}</h2>
<!--          <p>{{ val.sub_title }}</p>-->
        </div>
      </van-swipe-item>
    </van-swipe>
    <van-pull-refresh v-model="$store.state.home.refreshing" @refresh="onRefresh">
      <van-list
          v-model="$store.state.home.loading"
          :finished="$store.state.home.finished"
          finished-text="没有更多了"
          @load="onLoad"
      >
        <van-cell v-for="(item,index) in list" :key="index" class="content">
          <router-link :to='{name:"detail",params:{id:item._id},query:{toPath:"home"}}'>
            <h2>{{ index + 1 }}.{{ item.title }}</h2>
            <p>{{ item.des }}</p>
          </router-link>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import '../assets/scss/Home.scss'
import {mapState, mapActions, mapMutations} from 'vuex'

// const _ = require('lodash');
import {throttle} from 'lodash/function'

export default {
  name: "home",
  components: {},
  data() {
    return {
    }
  },
  mounted() {
    this.banner();

  },
  methods: {
    ...mapActions('home', ['home','banner']),
    onLoad: throttle( function (){
        this.home({_page: parseInt(this.$store.state.home.list.length / 13),q:'已审核',_name:"audit"})
      }, 500),
    onRefresh() {
      this.onLoad();
    },
  },
  computed: mapState('home', {
    list: state => state.list,
    bannerData:state=>state.banner
  })
}
</script>
